body {
  margin-left: 20px;
  padding: 0;
  font-family: sans-serif;
}
.error {
  color: red;
  background: lightgrey;
  font-size: 20px;
  border-style: solid;
  border-radius: 5px;
  padding: 10px;
  margin-bottom: 10px;
}
.success {
  color: green;
  background: lightgrey;
  font-size: 20px;
  border-style: solid;
  border-radius: 5px;
  padding: 10px;
  margin-bottom: 10px;
}
input {
  margin-bottom: 5px;
}
.lisays {
  display: inline-block;
  margin-right: 100%;
  border: solid black;
}



